import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'

Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err);
}
  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
	redirect:'/sy',
    children:[
      {
        path: 'sy',
        name: 'sy',
        component: () => import('../views/sy.vue')
      },{
        path: 'ks',
        name: 'ks',
        component: () => import('../views/ks.vue')
      },{
        path: 'lx',
        name: 'lx',
        component: () => import('../views/lx.vue')
      },{
        path: 'kc',
        name: 'kc',
        component: () => import('../views/kc.vue')
      },{
        path: 'jf',
        name: 'jf',
        component: () => import('../views/jf.vue')
      },{
        path: 'cj',
        name: 'cj',
        component: () => import('../views/cj.vue')
      },{
        path: 'ctb',
        name: 'ctb',
        component: () => import('../views/ctb.vue')
      },{
        path: 'xgmm',
        name: 'xgmm',
        component: () => import('../views/xgmm.vue')
      }
    ]
  },
  {
	  path: '/login',
	  name: 'login',
	  component: () => import('../views/login.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
